.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Cam {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}
.Cam-header {
  display: flex;
  background-color: #335cad;
  min-height: 20vh;
  
}
.mycam-sectiontable{
  display: grid;
  grid-template-columns: auto auto auto; 
  max-width: 99em;
  background-color: #1058aa;
  text-align: center;

}
.mycams-video{
  flex:auto;
  background-color: #b6317a;
  min-height: 30vh;
  display: flex;
  flex-direction: row;
  color: white;
  text-align: center;
}
